@charset "UTF-8";
//main
.main_container {
    overflow:hidden;
    .header {
        width:100%; height:50px; background-color:#000; position:relative;
        h1 { font-size:16px; text-align:center; color:#FFF; font-weight:500; padding:17px 0 0; }
    }
    .menu_wrap {
        min-width:1200px; height:calc(100vh - 80px); overflow:hidden; background-color:#000;
        .menu_li {
            width:29%; height:100%; background-repeat:no-repeat; float:left; position:relative;
            .logo { margin:0 auto; text-align:center; }
            .sub_wrap { display:none; }
            &.on {
                &:nth-of-type(1) { background-image:url(../../images/main/img01_hover.jpg); background-position:50% 50%; }
                &:nth-of-type(2) { background-image:url(../../images/main/img02_hover.jpg); }
                &:nth-of-type(3) { background-image:url(../../images/main/img03_hover.jpg); }
                &:nth-of-type(4) { background-image:url(../../images/main/img04_hover.jpg); }
                .sub_wrap {
                    width:100%; height:100%; position:absolute; z-index:1; text-align:center; top:0; overflow:hidden; display:block;
                    .coming_wrap {
                        padding:370px 0 0;
                        strong { width:500px; margin:0 auto; display:block; text-align:center; font-size:53px; color:#FFF; font-weight:600; }
                    }
                    .sub_box {
                        width:1020px; padding:155px 0 0; display:inline-block;
                        .lg_content {
                            padding:80px 0 0;
                            .lg_wrap {
                                float:left;
                                &:nth-of-type(2) {
                                    padding:0 0 0 120px;
                                    .lg_box:last-of-type { margin:0 0 0 45px; }
                                }
                                strong { width:100%; display:block; text-align:center; color:#FFF; font-size:26px; font-weight:500; }
                                .lg_box {
                                    padding:60px 0 0; float:left;
                                    &:hover {
                                        img { transform:scale(1.15); }
                                    }
                                    .img_wrap {
                                        width:280px; height:180px; background-color:#FFF; overflow:hidden; border:1px solid #FFF; box-sizing:border-box;
                                        img { width:100%; transition:all, .2s; }
                                    }
                                    p { padding:23px 0 0; color:#FFF; font-size:21px; font-weight:300; }
                                }
                            }
                        }
                        ul {
                            padding:170px 0 0;
                            &:after { content:""; display:block; clear:both; }
                            li {
                                float:left; margin:0 0 0 45px;
                                &:first-child { margin:0; }
                                &:hover {
                                    img { transform:scale(1.15); }
                                }
                                .img_wrap {
                                    width:280px; height:180px; background-color:#FFF; overflow:hidden; border:1px solid #FFF; box-sizing:border-box;
                                    img { width:100%; transition:all, .2s;}
                                }
                                p { padding:23px 0 0; color:#FFF; font-size:21px; font-weight:300; }
                            }
                        }
                    }
                    &:after {
                        width:100%; height:100%; display:block; content:""; position:absolute; z-index:-1;
                        background-color:#000; opacity:.7; top:0; left:0;
                    }
                }
            }
            .logo {
                img { width:100%; }
            }
            &:last-child { width:13%; }
            &:nth-of-type(1) {
                background-image:url(../../images/main/img01.jpg); background-position:0.4% 74%;
                .logo { width:130px; padding:70px 0 0; }
            }
            &:nth-of-type(2) {
                background-image:url(../../images/main/img02.jpg); background-position:34% 74%;
                .sub_wrap {
                    .sub_box {
                        width:500px; padding:165px 0 0;
                        ul {
                            li {
                                width:280px; float:none;
                                &:first-child { margin:0 auto; }
                            }
                        }
                    }
                }
                .logo { width:300px; padding:85px 0 0; }
            }
            &:nth-of-type(3) {
                background-image:url(../../images/main/img03.jpg); background-position:5% 50%;
                .logo { width:120px; padding:70px 0 0; }
            }
            &:nth-of-type(4) {
                .logo {
                    width:80%; padding:400px 0 0;
                    img { width:100%; }
                }
            }
        }
    }
    .footer {
        width:100%; position:absolute; bottom:0; z-index:99;
        img { width:100%; }
    }
}
//ces2021
.ces2021_container {
    overflow:hidden;
    .ces_wrap { width:100%; height:100vh; background:url(../../images/main/ces2021_bg.jpg) 50% 50% no-repeat; background-size:cover; }
    .footer {
        width:100%; position:absolute; bottom:0; z-index:99;
        img { width:100%; }
    }
}
//login
.login_container {
    width:100%; height:100vh; background-color:#DEDEE0;
    .login_wrap {
        padding:150px 0 0;
        .img_wrap {
            width:770px; margin:0 auto; padding:150px; background-color:#DEDEE0; position:relative;
            .link_btn { width:90px; height:35px; position:absolute; bottom:185px; left:325px; }
        }
    }
}
//archive
.archive_container {
    .archive_wrap {
        width:1140px; padding:0 0 50px; margin:0 auto; text-align:center;
        strong { padding:100px 0 50px; display:block; font-size:30px; }
        section {
            position:relative; border-top:1px solid #E4E4E4;
            h1 { padding:30px 0 30px 90px; font-size:18px; font-weight:600; text-align:left; }
            .swiper_cont {
                width:980px;
                .swiper_wrap {
                    .swiper_slide {
                        .video_wrap {
                            img { width:100%; }
                        }
                        p { padding:25px 0; text-align:left; font-size:14px; line-height:20px; }
                    }
                }
            }
            .swiper-button-next, .swiper-button-prev {
                &:after { display:none; }
                width:12px; height:20px; background:url(../../images/main/next_btn.jpg) 0 no-repeat; background-size:cover; right:40px; top:140px;
            }
            .swiper-button-prev { left:40px; transform:rotate(180deg); }
        }
    }
    .footer {
        width:100%;
        img { width:100%; }
    }
}

